import styled from "styled-components";

const Button = styled.button`
    width: 30px;
    height: 30px;
    position: fixed;
    bottom: 100px;
    right: 100px;
    background: #00273d;
    font-size: 12px;
    color: #fff;
    cursor: pointer;
`;

export default function FloatingTopButton() {
    const gotoTop = () => {
        window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
    };
    return <Button onClick={gotoTop}></Button>;
}
