import "../css/component/Header.scss";
import { Link, useLocation, useNavigate } from "react-router-dom";
import styled from "styled-components";
import useScroll from "../custom_hooks/useScroll.tsx";
import ImgLogo01 from "../assets/img/common/logo_01.png";
import ImgLogo02 from "../assets/img/common/logo_02.png";

interface HeaderProps {
    isEmail: boolean;
}

const headerMargin = 0;

const HeaderWrapper = styled.header`
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    justify-content: center;
    width: 100%;
    height: 60px;
    background: #ffffff00;
    z-index: 1;
    padding-top: 23px;
    transition: all 0.3s linear;
`;
const Term = styled.span`
    width 40vw;
`;

const List = styled.ul`
    display: inline-flex;
    justify-content: right;
    list-style: none;
    margin-right: ${headerMargin}px;
`;
const Item = styled.li`
    margin-left: 40px;
    text-transform: uppercase;
    font-size: 16px;
    color: ${(props) => (props.selected ? "black" : "white")};
`;
const Logo = styled.img`
    display: inline;
    width: 130px;
    margin-left: ${headerMargin}px;
    cursor: pointer;
    transition: all 0.3s linear;
`;

const EmailWrapper = styled.a`
    display: inline;
    cursor: pointer;
    font-size: 14px;
    color: #ccc;
    text-decoration: none;
    margin-left: 20px;
`;
const EmailImg = styled.img`
    width: 30px;
`;

export default function Header(props) {
    const { pathname } = useLocation();
    const { y } = useScroll();
    const isFloating: boolean = y > 0;
    const navigate = useNavigate();
    const isEmail = props.isEmail || true;

    return (
        <HeaderWrapper id="header-pc" className={isFloating ? "float" : ""}>
            <Logo
                src={isFloating ? ImgLogo01 : ImgLogo02}
                onClick={() => {
                    navigate("/");
                }}
            />
            <Term />
            <List>
                <Item selected={pathname.startsWith("/introduction")}>
                    <Link to="/introduction"></Link>
                </Item>
                <Item selected={pathname.startsWith("/production")}>
                    <Link to="/production"></Link>
                </Item>
                <Item selected={pathname.startsWith("/service")}>
                    <Link to="/service"></Link>
                </Item>
            </List>

            {isEmail ? (
                <EmailWrapper href="mailto:jinsun.yang@pluspro.kr">
                    <EmailImg /> 
                </EmailWrapper>
            ) : null}
        </HeaderWrapper>
    );
}
